.ProjectBlock {
  .imagesProjectBlock {
    width: 100%;
    height: 300px;
    overflow: hidden;
    cursor: pointer;

    .imagesProject {
      width: 100%;
      height: 100%;
      background-size: cover;
      background-position: 50% 50%;
      position: relative;
      transition: all .8s;
      z-index: 4;

      &:hover {
        transform: scale(1.1);
      }

      &::before {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: black;
        transition: all .8s;
        opacity: 0;

      }

      &:hover::before {
        opacity: 0.4;
      }
    }

    .buttonBlock {
      width: 100%;
      height: 100%;
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      opacity: 0;
      transition: all .8s;
    }

    .buttonBlock span {
      font-family: "Josefin Sans", sans-serif;
      position: relative;
      display: inline-block;
      padding: 9px 20px 6px 20px;
      font-size: 15px;
      text-transform: uppercase;
      line-height: 1.5em;
      color: #fff;
      border: 1px solid #fff;
      border-radius: 30px;
      text-decoration: none;
      -webkit-transition: all .3s ease-out !important;
      transition: all .3s ease-out !important;
      //display: inline-block;
      //border-radius: 5px;
      //padding: 4px;
      //color: #fff;
      //font-size: 20px;
      //font-family: 'Playfair Display', serif;
      font-weight: 400;
      //line-height: 1.75em;
      //background: rgb(156, 84, 81, 0.8);;
    }

    &:hover .buttonBlock {
      opacity: 1;
    }
  }

  .titleProject {
    font-size: 20px;
    font-family: 'Playfair Display', serif;
    font-weight: 400;
    line-height: 1.75em;
    color: #9c5451;
  }

  .aboutProject {
    font-family: 'Mukta', sans-serif;
    font-size: 14px;
    line-height: 1.75em;
    font-weight: 400;
    color: #999;
  }
  .stackProject{
     span {
       color: #9c5451;
     }
  }

  width: 350px;
  height: 100%;

}

@media screen and (max-width: 1125px)  {
  .ProjectBlock {
    margin-bottom: 30px;
  }
}



